
.fillcontain {
  width: 100%;
  height: 80%;
  // overflow: auto;
  padding: 5px;
  box-sizing: border-box;

  /*列表区域滚动显示 */
  ::v-deep .el-table__body-wrapper {
    height: 55vh;
    overflow: auto;
  }
  .btnAdd {
    overflow: hidden;
    float: right;
  }
  /* 分页器 */
  .pagination {
    text-align: right;
    margin-right: 20px;
    margin-top: 10px;
  }
}
